import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@src/components/Hello'
import Index from '@src/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/*',
      redirect: '/index'
    }
  ]
})



