<template>
  <div class="index">
  	<h2>{{ msg }}</h2>
    <h1>{{ goodsinfo }}</h1>
  </div>
</template>

<script>
// import backgroundData from "D:/vue-project1/data.json"
export default {
  name: 'index',
  props: {
    goodsinfo: {
      type: Object
    }
  },
  created: function () {
  	this.classMap = ['decrease','discount','special','invoice','guarantee',];
  },
  data: function () {
  	return {
  		msg: 'this is index'
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.index {

}
</style>



